<template>
    <div class="registration">
        <h2 class="registration__title">Регистрация</h2>
        <p class="registration__text">Уже есть аккаунт? <a href="#" class="registration__login-link">Войти</a> </p>

        <form action="#" class="registration__form form" @submit.prevent="sendFormData">
            <p class="form__field">
                <label for="name">Имя</label>
                <input @input="checkValidity" v-model="formData.name" type="text" id="name" name="name" placeholder="Введите Ваше имя">
                <span class="form__field-status"></span>
            </p>

            <p class="form__field">
                <label for="email">Email</label>
                <input @input="checkValidity" v-model="formData.email" type="email" id="email" name="email" placeholder="Введите ваш email">
                <span class="form__field-status"></span>
            </p>

            <p class="form__field">
                <label for="phone">Номер телефона</label>
                <input @input="checkValidity" type="text" id="phone" name="phone" placeholder="Введите номер телефона">
                <span class="form__field-status"></span>
            </p>

            <p class="form__field">
                <label for="lang" class="form__lang-label">Язык</label>
                <input @click="selectIsOpened = !selectIsOpened" type="text" id="lang" readonly placeholder="Язык" class="form__lang">
                <ul class="form__lang-list" v-show="selectIsOpened">
                    <li class="form__lang-item" data-lang="rus">Русский</li>
                    <li class="form__lang-item" data-lang="en">Английский</li>
                    <li class="form__lang-item" data-lang="cn">Китайский</li>
                    <li class="form__lang-item" data-lang="sp">Испанский</li>
                </ul>
            </p>

            <div class="form__policy">
                <input @change="checkValidity" type="checkbox" class="form__checkbox" id="policy" name="policy">
                <label for="policy">Принимаю <a href="#">условия</a> использования</label>
            </div>

            <button type="submit" class="form__submit" v-bind:disabled="!isValid">Зарегистрироваться</button>            
        </form>
    </div>
</template>

<script>
import mask from '../scripts/phoneMask'
import onlyTextInput from '../scripts/onlyTextInput'
export default {
    data() {
        return {
            formData: {
                name: '',
                email: '',
                phone: '',
                lang: ''
            },
            isValid: false,
            selectIsOpened: false
        }
    },
    mounted() {
        this.setLang()        
        onlyTextInput('#name')  
        this.setPhoneMask()  
        this.closeSelect()           
    },
    methods: {
        closeSelect() {
            document.addEventListener('click', (e) => {
                if (e.target.tagName !== 'INPUT' && e.target.tagName !== 'LI' && this.selectIsOpened) {
                    this.selectIsOpened = false;
                }
            })
        },
        setPhoneMask() {
            mask('#phone')

            const phone = document.querySelector('#phone')

            phone.addEventListener('input', () => {
                this.formData.phone = phone.value
            })
        },
        setLang() {
            const selectedLang = document.querySelector('.form__lang')
            const items = document.querySelectorAll('.form__lang-item')

            items.forEach(item => {
                item.addEventListener('click', () => {
                    const lang = item.getAttribute('data-lang')

                    this.formData.lang = lang
                    selectedLang.value = item.textContent
                    this.selectIsOpened = false;
                    this.checkValidity()
                })
            }) 
                       
        },
        checkValidity() {
            const name = document.querySelector('#name')
            const email = document.querySelector('#email:valid')
            const phone = document.querySelector('#phone')
            const lang = document.querySelector('#lang')            
            const checkbox = document.querySelector('#policy:checked')

            if (name.value && phone.value.length >= 18 && lang.value && checkbox && email) {
                this.isValid = true
            } else {
                this.isValid = false
            }

            if (phone.value.length < 18) {
                phone.classList.add('invalid')
            } else {
                phone.classList.remove('invalid')
            }
        },
        sendFormData() {
            // для теста
            console.log(this.formData)
        }
    }
}
</script>

<style scoped>
.registration {
    width: 100%;
    max-width: 460px;
    min-width: 360px;
    padding: 40px 30px;
    margin: 0 auto;
    background-color: #ffffff;
    border-radius: 24px;
    text-align: left;
}

a {
    text-decoration: none;
    color: #0880AE;
}

.registration__title {
    font-size: 34px;
    line-height: 44px;
}

.registration__text {
    font-size: 16px;
    line-height: 22px;
    margin-top: 8px;
}

.form {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    width: 100%;
}

.form__field {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    margin-bottom: 33px;
}

.form__field label {
    font-size: 16px;
    line-height: 20px;
}

.form__field input {
    outline: none;
    margin-top: 6px;
    padding: 16px;
    border: 1px solid #DBE2EA;
    border-radius: 6px;
    font-size: 16px;
    line-height: 20px;
}

.form__field input + span {
    display: none;
    position: absolute;
    bottom: -22px;
    left: 0;
}

.form__field input:invalid + span,
input.invalid + span {
    display: block;
}

.form__field input + span::after {
    content: "Введено не корректное значение";
    font-size: 14px;
    line-height: 18px;
    color: #FF7171;
}

.form__field input:focus {
    box-shadow: 0 0 0 2px #0880AE inset;
}

.form__field input::placeholder {
    color: #7C9CBF;
}

.form__lang-label {
    position: relative;
}

.form__lang-label::after {
    position: absolute;
    content: "";
    top: 45px;
    right: 25px;
    width: 10px;
    height: 10px;
    transform: rotate(-45deg);
    background-color: #fff;
    border: 3px solid #0880AE;
    border-right: none;
    border-top: none;
    z-index: 10;
}

.form__lang-list {
    position: absolute;
    width: 100%;
    top: 80px;
    left: 0;
    margin-top: 5px;
    border: 1px solid #DBE2EA;
    background-color: #ffffff;
    border-radius: 6px;
    z-index: 2;
}

.form__lang-list li {
    padding: 15px;
}

.form__lang-list li:hover {
    background: #EBF4F8;
}

.form__policy {
    display: flex;
}

.form__checkbox {
    display: none;
}

.form__policy label {
    position: relative;    
    padding: 7px;
    padding-left: 38px;
}

.form__policy label::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 28px;
    height: 28px;
    border: 1px solid #DBE2EA;
    border-radius: 4px;
    box-shadow: 0 0 5px 0 rgba(17, 15, 22, 0.2);
}

.form__checkbox:checked + label::before {
    border: 2px solid #0880AE;
}

.form__checkbox:checked + label::after {
    position: absolute;
    content: "";
    width: 14px;
    height: 7px;
    top: 7px;
    left: 7px;
    transform: rotate(-45deg);
    
    border: 3px solid #0880AE;
    border-top: none;
    border-right: none;
}

.form__submit {
    margin-top: 40px;
    padding: 18px;
    font-size: 16px;
    line-height: 20px;
    background-color: #0880AE;
    border: none;
    border-radius: 6px;
    color: #ffffff;
    outline: none;
}

.form__submit:disabled {
    background-color: #DBE2EA;
    color: rgba(17, 15, 22, 0.5);
}

.form__submit:hover {
    box-shadow: 0 24px 46px 0 rgba(17, 15, 22, 0.2);
} 

.form__submit:disabled:hover {
    box-shadow: none;
} 

.form__submit:active {
    box-shadow: 0 0 0 2px rgba(17, 15, 22, 0.8) inset;
}
</style>