const onlyTestInput = (selector) => {
    const input = document.querySelector(selector);

    input.addEventListener('keypress', function(e) {
        if (e.key.match(/[^а-яё A-z 0-9 -]/ig)) {
            e.preventDefault();                
        }
    });

    input.addEventListener('change', () => { 
        if (input.value.match(/[^а-яё A-z 0-9 -]/ig)) {
            input.value = '';                              
        }
    });

};

export default onlyTestInput;